import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';

import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import { useRef, useMemo } from 'react';
import axios from '../Request/RequestConfig.js';



export default function Editor() {
    const quillRef = useRef(null);

    const [title, setTitle] = useState('');
    const [content, setContent] = useState('');
    const [loginError, setLoginError] = useState('');
    const navigate = useNavigate();

    const imageHandler = () => {
        const input = document.createElement('input');
        input.setAttribute('type', 'file');
        input.setAttribute('accept', 'image/*');
        input.click();

        input.addEventListener('change', async () => {
            const file = input.files[0];

            try {

                const editor = quillRef.current.getEditor();
                const range = editor.getSelection();
                editor.setSelection(range.index + 1);
            } catch (error) {
                console.log(error);
            }
        });
    };

    const modules = useMemo(
        () => ({
        toolbar: {
            container: [
                [{ 'header': [1, 2, 3, 4, 5, 6, false] }],
                [{ 'font': [] }],
                [{ 'align': [] }],
                ['bold', 'italic', 'underline', 'strike', 'blockquote'],
                [{ 'list': 'ordered' }, { 'list': 'bullet' }, 'link'],
                [{ 'color': ['#000000', '#e60000', '#ff9900', '#ffff00', '#008a00', '#0066cc', '#9933ff', '#ffffff', '#facccc', '#ffebcc', '#ffffcc', '#cce8cc', '#cce0f5', '#ebd6ff', '#bbbbbb', '#f06666', '#ffc266', '#ffff66', '#66b966', '#66a3e0', '#c285ff', '#888888', '#a10000', '#b26b00', '#b2b200', '#006100', '#0047b2', '#6b24b2', '#444444', '#5c0000', '#663d00', '#666600', '#003700', '#002966', '#3d1466', 'custom-color'] }, { 'background': [] }],
                ['image', 'video'],
                ['clean'],
            ],
            handlers: { image: imageHandler },
            },
            clipboard: {
             matchVisual: false,
            },
        }),
        [],
    );

    const formats = [
        'header',
        'font',
        'size',
        'bold',
        'italic',
        'underline',
        'strike',
        'blockquote',
        'list',
        'bullet',
        'align',
        'image',
    ];

    const onChangeTitle = (event) => {
        const newTitle = event.target.value;
        if (newTitle.length <= 60) {
            setTitle(newTitle);
        } else {
            //       .
            // : alert   
            alert(' 60  .');
        }
    };

    const onChangeContent = (content) => {
        setContent(content);
    };

    const handleSubmit = async () => {
        try {
            const accessToken = localStorage.getItem('accessToken');
            const refreshToken = localStorage.getItem('refreshToken');
            console.log(accessToken);
            console.log(refreshToken);

            if (accessToken && refreshToken) {
                //  
                const response = await axios.post('/manager/shop/save', JSON.stringify({ title,content }), {
                    headers: {
                        'Content-Type': 'application/json',
                        'Authorization': `Bearer ${accessToken}`,
                        'RefreshToken': `Bearer ${refreshToken}`,
                    },
                });

                if (response.ok) {
                    //     
                    const data = await response.json();
                    console.log(data); //    
                    navigate('/shop');

                } else {
                    //     
                    const errorMessages = await response.clone().json();
                    console.log(errorMessages.errors);
                    const errors = errorMessages.errors;
                    for(const error of errors){
                        console.log(error.defaultMessage);
                        alert(error.defaultMessage);
                    }
                }
            } else {
                setLoginError('     .'); //    
            }
        } catch (error) {
            console.error('    ..', error);
            setLoginError('   .');
        }
    }

    return (
        <div style={{ display: 'flex', flexDirection: 'column', height: '100vh' }}>
            <input
                type="text"
                value={title}
                onChange={onChangeTitle}
                placeholder=""
                style={{ flex: 'none', padding: '10px', fontSize: '18px' }}
            />


            <div style={{ flex: '1', minHeight: '0', padding: '10px', fontSize: '14px', marginBottom: 'auto' }}>
                {/* <ReactQuill/>   div */}
                <ReactQuill
                    ref={quillRef}
                    formats={formats}
                    modules={modules}
                    theme="snow"
                    onChange={onChangeContent}
                    style={{ flex: '1', minHeight: '0', padding: '10px', fontSize: '14px', width: '100%', height: '70%' }}
                />
                <div dangerouslySetInnerHTML={{ __html: content }} style={{ display: 'none' }} />
            </div>


            <div style={{ display: 'flex', justifyContent: 'flex-end', marginTop: 'auto', marginRight: '10px', position: 'relative', top : '-200px' }}>
                <button onClick={handleSubmit} style={{ marginRight: '10px' }}></button>
                <button style={{}}></button>
            </div>

        </div>

    );
}
